
import React from 'react';
import { render, fireEvent, screen } from '@testing-library/react-native';
import GrammarExercise from '../../../../src/screens/exercises/grammar';

// Mock des dépendances
jest.mock('../../../../src/screens/exercises/grammar/hooks/useGrammar', () => ({
  __esModule: true,
  default: jest.fn(),
}));

jest.mock('@react-navigation/native', () => ({
  ...jest.requireActual('@react-navigation/native'),
  useNavigation: () => ({
    goBack: jest.fn(),
  }),
}));

jest.mock('expo-router', () => ({
  router: {
    push: jest.fn(),
  },
}));

const mockUseGrammar = require('../../../../src/screens/exercises/grammar/hooks/useGrammar').default;

describe('GrammarExercise', () => {
  const mockRoute = {
    params: {
      level: 'A1',
    },
  };

  beforeEach(() => {
    jest.clearAllMocks();
  });

  it('should render loading state initially', () => {
    mockUseGrammar.mockReturnValue({
      loaded: false,
      grammarData: [],
    });

    render(<GrammarExercise route={mockRoute} />);
    expect(screen.getByTestId('activity-indicator')).toBeTruthy();
  });

  it('should render the main components when loaded', () => {
    mockUseGrammar.mockReturnValue({
      loaded: true,
      grammarData: [{ title: 'Rule 1' }],
      ruleIndex: 0,
      exerciseIndex: 0,
      currentRule: { title: 'Rule 1', content: 'Content 1' },
      currentExercise: { type: 'multiple-choice', question: 'Question 1' },
      completedExercises: {},
      showDetailedProgress: false,
      canCheckAnswer: true,
      isFirstExercise: true,
      isLastExercise: false,
      showFeedback: false,
      attempts: 0,
    });

    render(<GrammarExercise route={mockRoute} />);

    expect(screen.getByText('Rule 1')).toBeTruthy();
    expect(screen.getByText('Question 1')).toBeTruthy();
  });

  it('should call changeRule when a rule is selected', () => {
    const changeRule = jest.fn();
    mockUseGrammar.mockReturnValue({
      loaded: true,
      grammarData: [{ title: 'Rule 1' }, { title: 'Rule 2' }],
      ruleIndex: 0,
      changeRule,
      // ... autres états
    });

    render(<GrammarExercise route={mockRoute} />);
    fireEvent.press(screen.getByText('Rule 2'));
    expect(changeRule).toHaveBeenCalledWith(1);
  });

  it('should call submitAnswer when check answer button is pressed', () => {
    const submitAnswer = jest.fn();
    mockUseGrammar.mockReturnValue({
      loaded: true,
      grammarData: [{ title: 'Rule 1' }],
      currentExercise: { type: 'multiple-choice', question: 'Question 1' },
      canCheckAnswer: true,
      submitAnswer,
      // ... autres états
    });

    render(<GrammarExercise route={mockRoute} />);
    fireEvent.press(screen.getByText('Vérifier'));
    expect(submitAnswer).toHaveBeenCalled();
  });
});
